body, div, table, label, button, span, a, p, legend, input, pre {
  text-decoration: none;
  /*font-family: IRANSans-web!important;*/
  /*font-family: "Fira Code Light";*/
  /*font-family: Vazir!important;*/
  font-family: Vazir !important;
  text-align: right;
}


.MuiInputLabel-formControl {
  right: 0;
  left: unset;
}

.MuiInput-underline:before {
  border-bottom: 1px solid rgba(255, 255, 255, 0.42) !important;
}

.MuiFormLabel-root {
  color: rgba(255, 255, 255, 0.54) !important;
}

.MuiInputBase-root {
  color: rgba(255, 255, 255, 0.87) !important;
}


body {
  direction: rtl;
  text-align: right;
}
body{
  background-color: rgb(44, 49, 61);
  counter-reset: headings;
  //color: white;
}
.bar-chart-name {
  line-height: 1.5;
  font-weight: 500;
  text-transform: capitalize;
  color: rgb(255, 255, 255);
}

.card {
  border-radius: 0px;
}

.dark-card {
  background: rgb(21, 27, 36);

}

.btn-light-outline {
  background: transparent;
  border: 1px solid white;
  border-radius: 3px;
  color: white;
  padding: 0px 12px;
  height: 36px;
}

hr {
  border-top: 1px solid rgba(255, 255, 255, 0.28);
}

.section-detail {
  color: white;

  .num {
    color: #5a5d64;
    font-size: 35px;
  }

  .line {
    border-left: 1px solid #5a5d64;
    margin: 10px;
    height: 35px;
  }

  .title {
    font-size: 22px;
    font-weight: bold;
  }
}

.small-section-detail {
  color: white;

  .num {
    color: #5a5d64;
    font-size: 28px;
  }

  .line {
    border-left: 1px solid #5a5d64;
    margin: 10px;
    height: 30px;
  }

  .title {
    font-size: 17px;
    font-weight: bold;
  }
}

.question-line {
  color: white;
  font-size: 14px;
}

.dark-table {
  border-bottom: 2px solid #dee2e694;
  border-left: 2px solid #dee2e694;
  border-right: 2px solid #dee2e694;

  thead tr {
    background-color: #4f545ba3;
    color: white;
  }

  thead th {
    border-bottom: unset;
    border-top: 2px solid #dee2e694;
  }

  thead td {
    border-top: 2px solid #dee2e694;
  }

  tbody td {
    color: white;
    font-size: 12px;
  }
}

.more-detail {
  color: white;

  span {
    color: #2BD784;
    margin-right: 10px;
    margin-left: 10px;
  }
}

.more-detail-red {
  color: white;

  span {
    color: #E64141;
    margin-right: 10px;
    margin-left: 10px;
  }
}


.analysis {
  font-size: 14px;
  color: white;
  margin-bottom: 5px;
}

.next-page {
  color: rgb(13, 110, 241) !important;
  font-size: 16px;
  cursor: pointer;

  &:hover {
    text-decoration: underline !important;
  }
}
